.profileRightbar {
  flex: 1;
  background-color: rgb(0, 0, 0);
  padding-left: 40px;
  padding-top: 0px;
  color: white;
  padding-left: 50px;
  height: 100vh;
  overflow: hidden;
}

.subProfileRightbar {
  background-color: rgb(0, 0, 0);
  padding-top: 20px;
  color: white;
  display: flex;
  margin-left: 80px;
}

.profilePostContainer {
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
}

.ProfileUpdateText {
  font-weight: 300;
}

.imageforExplore {
  position: relative;
}

.imageforimage {
  width: 100%;
  height: 29vh;
  object-fit: cover;
}

.coveredLikesAndComments {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  font-size: 1.2rem;
  height: 29vh;
  font-weight: 600;
  visibility: hidden;

}

.logoForExplore {
  width: 20px;
  height: 20px;
}

.carouselIcon {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
}

.imageforExplore:hover .coveredLikesAndComments {
  visibility: visible;
}